.App {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

/* Layout */
.app-layout {
  display: grid;
  grid-template-columns: auto 1fr 2fr;
  height: 100vh;
  width: 100%;
}

/* Mobile Menu Toggle */
.menu-toggle {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 100;
  display: none;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}

/* Navigation Menu */
.nav-menu {
  background-color: white;
  height: 100vh;
  width: 64px;
  transition: width 0.3s;
  overflow: hidden;
}

.nav-menu.show {
  width: 240px;
}

/* Email List */
.email-list {
  height: 100vh;
  overflow-y: auto;
  border-right: 1px solid #e5e7eb;
  background-color: white;
}

/* Email Detail */
.email-detail {
  padding: 2rem;
  height: 100vh;
  overflow-y: auto;
  background-color: white;
}

/* Back to list button for mobile */
.back-to-list {
  display: none;
  margin-bottom: 1rem;
  padding: 0.5rem;
  background: none;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  color: #4b5563;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .app-layout {
    grid-template-columns: 1fr;
  }

  .menu-toggle {
    display: block;
  }

  .nav-menu {
    position: fixed;
    left: -240px;
    z-index: 50;
  }

  .nav-menu.show {
    left: 0;
  }

  .email-detail {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 40;
    transform: translateX(100%);
    transition: transform 0.3s;
  }

  .email-detail.show {
    transform: translateX(0);
  }

  .back-to-list {
    display: block;
  }
}